'use strict';

export default class Character {
  constructor(options) {
    this._el = options.el;

    this._render();
  }

  _move(down, right) {
    this._top += down;
    this._left += right;

    this._top = Math.max(0, this._top);
    this._left = Math.max(0, this._left);


    this._el.style.top = `${this._top}px`;
    this._el.style.left = `${this._left}px`;
  }

  _arrowKeyPress(e) {
    let key = e.code;

    switch (key) {
      case "ArrowDown":
        this._move(30, 0);
        return;
      case "ArrowUp":
        this._move(-30, 0);
        return;
      case "ArrowLeft":
        this._move(0, -30);
        return;
      case "ArrowRight":
        this._move(0, 30);
        return;
      default:
        return;
    }

  }

  _render() {
    this._top = 0;
    this._left = 0;
    this._move(0, 0);
    document.addEventListener('keydown', this._arrowKeyPress.bind(this));
  }
}



